import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        userCode:'',
        
		
    },
    mutations: {
		
		
        copy(state,cont){
            //
         
            state.userCode = cont;
        },
        change(state,contObj){
            //
            let str = contObj.str;
            let cont = contObj.cont;
            state[str] = cont;
        },
    },
    actions:{
		
        copeFun:function(context,mData){
            context.commit('copy',mData)
        }
        
		
    }
})

export default store