import request from './js_sdk/cooke-request/request/request.js'
 //const ServerUrl = "http://192.168.11.116";
//const ServerUrl = "http://test.144f.com:8080/sl-web"
const ServerUrl = "http://app.ecfpros.com:8080/sl-web"

request.setConfig({
  baseUrl: ServerUrl, // 
  dataType: 'json', // json
  responseType: 'text', // text
  // header
  header: {
    // 'token': '',
	// 'MYTK': uni.getStorageSync('token'),
	// 'Authorization': uni.getStorageSync('token'),
    'content-type': 'application/json' 
  }
})
// 
request.interceptors.request(config => {
  // 

  // 
  // config.header['content-type'] = 'application/json';
  // config.header.token = '';
     config.header.MYTK = uni.getStorageSync('token');
  
  // 'Authorization': uni.getStorageSync('token'),
  // 'content-type': 'application/json' 
  // 
  // config.header = {
    // 'content-type': 'application/json',
    // 'token': 'token from interceptors'
  // }

  // return false; // 
  // return Promise.reject('error from request interceptors'); // catch
  return config; // 
})
request.interceptors.response(res => {
  // 
  // ......
  
  // return false;    // ,
  // return {message: ''};   // 
  // return Promise.reject('error from response interceptors') // catch
    if (res.statusCode == '500'){
    	uni.showToast({
    		title: res.data.message,
    		icon: 'none',
    		duration: 1000
    	})
    	return false
    }
	if (res.data.status == 80){
		uni.redirectTo({
			url: '/pages/register/login'
		})
	}
	if (res.data.status == 1){
		uni.showToast({
			title: res.data.message,
			icon: 'none',
			duration: 2000
		})
		return false
	}
	if (res.data.status == 2){
		uni.showToast({
			title: res.data.message,
			icon: 'none',
			duration: 2000
		})
		return false
	}
	if (res.data.status == 0){
		return res; //  
	}
	 
  
  
},err =>{
	console.log(err)
	uni.showToast({
		title: err.response.data.message
	});
	return Promise.reject(err)
})
export default request